@import url(https://fonts.googleapis.com/css?family=Montserrat);
h1, h2, h3, h4, .btn {
  letter-spacing: .3px;
}
.router {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
body {
  overflow: hidden;
  margin: 0;
}
* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
#app {
  font-family: 'Montserrat', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: relative;
  height: 100vh;
  width: 100vw;
}
.global-container {
  display: block;
  padding: 50px 5% 50px 5%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  position: relative;
}
.global-container .btn {
    font-weight: 500;
    font-size: 17px;
    background-color: #00CFC9;
    color: #fff;
    border: 2px solid transparent;
    border-radius: 30px;
    padding: 15px 25px;
    margin-top: 30px;
    cursor: pointer;
    min-width: 200px;
    -webkit-transition: .3s ease-in-out;
    transition: .3s ease-in-out;
}
.global-container .btn:hover {
      background-color: transparent;
      border: 2px solid #00CFC9;
      color: #00CFC9;
}
.fade-slide-enter-active,
.fade-slide-leave-active {
  -webkit-transition: all 1s ease;
  transition: all 1s ease;
}
.fade-slide-enter {
  opacity: 0;
  -webkit-transform: translate3d(-5%, 0, 0);
          transform: translate3d(-5%, 0, 0);
}
.fade-slide-leave-to {
  opacity: 0;
  -webkit-transform: translate3d(5%, 0, 0);
          transform: translate3d(5%, 0, 0);
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: 1s all;
  transition: 1s all;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.home {
  background: pink;
  min-height: 100vh;
  background: url(static/img/fond-ecran1.2541ab3.png) no-repeat right center;
  background-size: contain;
}
@media (max-width: 1200px) {
.home {
      background-size: cover;
}
}
.home .apropos {
    color: #00CFC9;
    position: absolute;
    font-size: 20px;
    cursor: pointer;
}
.home .apropos span {
      position: relative;
}
.home .apropos span::after {
        content: '';
        position: absolute;
        height: 2px;
        width: 0;
        background-color: #00CFC9;
        left: 0;
        bottom: -5px;
        -webkit-transition: .3s ease-in-out;
        transition: .3s ease-in-out;
}
.home .apropos span:hover::after {
        width: 100%;
}
.home .container {
    height: 100%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    min-height: calc(100vh - 100px);
}
.home .container > * {
      padding: 50px 0;
}
@media (max-width: 1200px) {
.home .container > * {
        padding: 50px 0;
}
}
.home .container .left-wrapper {
      -webkit-box-flex: 60%;
          -ms-flex: 60%;
              flex: 60%;
      -ms-flex-negative: 1;
          flex-shrink: 1;
}
@media (max-width: 1200px) {
.home .container .left-wrapper {
          -ms-flex-preferred-size: 100%;
              flex-basis: 100%;
}
}
.home .container .left-wrapper .title {
        color: black;
        font-weight: bold;
        font-size: 25px;
        letter-spacing: 1px;
        line-height: 1.5em;
}
.home .container .left-wrapper .content {
        margin-top: 30px;
        font-weight: 100;
        line-height: 1.5em;
}
.home .container .right-wrapper {
      -webkit-box-flex: 40%;
          -ms-flex: 40%;
              flex: 40%;
      -ms-flex-negative: 2;
          flex-shrink: 2;
}
.home .container .right-wrapper img {
        width: 100%;
}
.home .mentions-legales {
    position: absolute;
    right: 20px;
    bottom: 20px;
    cursor: pointer;
}
@media (max-width: 1200px) {
.home .mentions-legales {
        display: block;
        position: relative;
        text-align: center;
}
}
.home .mentions-legales span {
      -webkit-transition: .3s ease-in-out;
      transition: .3s ease-in-out;
      color: #B3B3B3;
}
.home .mentions-legales span:hover {
        color: black;
}
.slide {
  max-height: 100vh;
  overflow: hidden;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: top right;
  min-height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.slide .choices {
    position: relative;
}
.slide .choices .choice {
      position: absolute;
}
.slide .choices .choice:hover .name {
        opacity: 1;
}
.slide .choices .choice img {
        background-color: #E8E9EA;
        padding: 30px 10px;
        border-radius: 100%;
}
.slide .choices .choice .name {
        opacity: 0;
        top: 10px;
        position: relative;
        text-align: center;
        color: #B3B3B3;
        -webkit-transition: 0.3s ease-in-out;
        transition: 0.3s ease-in-out;
}
.slide .counter {
    position: absolute;
    right: 5%;
    top: 5%;
}
.slide .counter .count-current {
      color: #00CFC9;
      font-size: 2rem;
      position: relative;
}
.slide .counter .count-current::after {
        content: '';
        height: 1px;
        background-color: black;
        position: absolute;
        left: 50%;
        right: 0;
        bottom: 0;
}
.slide .counter .count-total {
      color: black;
      top: 5px;
      position: relative;
      text-align: right;
      font-weight: 100;
      width: 100%;
      display: block;
}
